
import { useState, useEffect } from 'react';
import { Chrono } from 'react-chrono';
import axios from "axios";
import _ from 'lodash'
import { Button } from 'reactstrap';

import { TL_MODE, URL_APPEND } from '@general/constants'
// import sampledata from '../../data';
import SelectBill from './components/SelectBIll';
import Loading from '@general/loading';

import dayjs from 'dayjs'
import advancedFormat from 'dayjs/plugin/advancedFormat'
dayjs.extend(advancedFormat)

const BillTracker = (props) => {
    const [data, setData] = useState([]);
    const [cdata, setCdata] = useState([]);
    const [selBill, setSelBill] = useState({});
    const [chrono, setChrono] = useState();
    const [isLoading, setIsLoading] = useState(true);
    const [hasDeleted, setHasDeleted] = useState(false);

    let trackerUrl = `${URL_APPEND}/api/tracker/list`, deleteUrl = `${URL_APPEND}/api/tracker/delete`;

    let deleteTracker = (uid) => {
        let token = document.head.querySelector('meta[name="csrf-token"]');
        if (token) axios.defaults.headers.common['X-CSRFToken'] = token.content;

        axios.delete(`${deleteUrl}/${uid}`)
            .then(res => {
                setHasDeleted(true);
            })
            .catch(err => console.error('Tracker: ', err));
    }

    let mapFunc = obj => ({
        bill_id: obj.bill,
        title: dayjs(obj.stage_date).format('Do MMM YYYY'),
        cardSubtitle: <>
            <a href={`${URL_APPEND}/bills/${obj.bill_slug}`}>{obj.bill_name} &nbsp;&nbsp;<i className="fas fa-external-link-alt"></i></a>
            {props.isadmin &&
                <Button style={{ border: '0' }} className='float-right' color='danger' outline size='sm' onClick={() => {
                    deleteTracker(obj.uuid)

                }}>
                    &times;
            </Button>
            }
        </>,
        cardDetailedText: <>
            <><b>{obj.stage}</b> </><br />
            {obj.details}
            <hr style={{ color: 'red', borderTop: '1px dotted red' }} />
            <i>sponsor: {obj.bill_sponsor}</i><br />
            {obj.bill_purpose}
        </>
    })

    useEffect(() => {
        // setData(sampledata)
        axios.get(trackerUrl)
            .then(res => {
                let remapped = res.data.map(mapFunc)
                setData(remapped)
                setCdata(remapped)
                setIsLoading(false);
            })
            .catch(err => console.log('Tracker: ', err));
    }, [])

    useEffect(() => {
        hasDeleted && axios.get(trackerUrl)
            .then(res => {
                let remapped = res.data.map(mapFunc)
                setData(remapped);
                setSelBill({ ...props.selBill });
                hasDeleted && setHasDeleted(false);
            })
            .catch(err => console.log(err));
    }, [hasDeleted])

    useEffect(() => {
        let sb = props.selBill ? props.selBill.value : selBill.value
        setCdata(data.filter(f => f.bill_id == sb))
        setChrono(<></>)
    }, [selBill, props.selBill])

    useEffect(() => {
        props.performedAjax && axios.get(trackerUrl)
            .then(res => {
                let remapped = res.data.map(mapFunc)
                setData(remapped);
                setSelBill(props.selBill);
                props.setPerformedAjax && props.setPerformedAjax(false);
            })
            .catch(err => console.log(err));
    }, [props.performedAjax])

    useEffect(() => {
        if (props.hideInitBills && _.isEmpty(props.selBill)) {
            setChrono(<></>)
        } else {
            cdata.length > 0 && setChrono(<Chrono
                items={cdata}
                theme={{ primary: "red", secondary: "white", cardBgColor: "white", cardForeColor: "black " }}
                slideShow
                slideItemDuration={2500}
                scrollable
                mode={TL_MODE[props.chronoMode ? props.chronoMode : 'v']}>
                {/* {cdata.map(b=>{
                        return <div style={{  width: '100%' }}>
                            {b.cardSubtitle}
                            <br/>
                            {b.cardDetailedText}
                        </div>
                    })} */}
            </Chrono>)
        }
    }, [cdata])


    return isLoading ? <Loading /> : (
        <>
            {!props.hideBillSelector &&
                <div className='row'>
                    <div className='col-md-10'>

                        <SelectBill onChange={sel => {
                            setSelBill(sel)
                        }} />
                    </div>
                    <div className='col-md-2'>
                        <button className='btn btn-success' onClick={() => {
                            setChrono(<></>)
                            setCdata(data)
                        }}>Refresh</button>
                    </div>
                </div>
            }

            {cdata && cdata.length > 0 && chrono}


            <h1 style={{ color: '#D6DBDF', textAlign: 'center', display: cdata.length > 0 && 'none' }}>
                {/* ¯\_(ツ)_/¯  */}
                乁( ⁰͡  Ĺ̯ ⁰͡ ) ㄏ
                <br />No Data
            </h1>
            {/* <img src='/timeline.svg' alt='timeline'></img> */}


        </>
    )
}

export default BillTracker;
